import * as types from './actionTypes';
import * as api from '../api/obtainedApi';

export function obtainIngredient(ingredient) {
  return function (dispatch) {
    return dispatch({
      type: types.OBTAINED_INGREDIENT_SUCCESS,
      obtained: api.obtainIngredient(ingredient, false)
    });
  };
}

export function unobtainIngredient(ingredient) {
  return function (dispatch) {
    return dispatch({
      type: types.OBTAINED_INGREDIENT_UNDO_SUCCESS,
      obtained: api.obtainIngredient(ingredient, true)
    });
  };
}


