/* eslint-disable import/no-named-as-default */
import React from 'react';
import PropTypes from 'prop-types';
import { routes } from '../constants/routes';
import { Switch, Route } from 'react-router-dom';
import HomePage from './HomePage';
import RecipesPage from './Recipes/Recipes';
import RecipePage from './Recipes/Recipe';
import ListPage from './ListBuilder/ListBuilder';

// This is a class-based component because the current
// version of hot reloading won't hot reload a stateless
// component at the top-level.

class App extends React.Component {
  render() {
    return (
      <div className="stage">
        <Switch>
          <Route exact path="/" component={HomePage}/>
          <Route path={routes.recipes} component={RecipesPage}/>
          <Route path={routes.list} component={ListPage}/>
          <Route path={routes.recipe} component={RecipePage}/>
        </Switch>
      </div>
    );
  }
}

App.propTypes = {
  children: PropTypes.element
};

export default App;



