// reports to the server that a user has crossed off an element from their shopping list.
let obtained = [];

export function obtainIngredient(ingredient, undo){
  let item = obtained.find(i => i.id === ingredient.id);
  if (!undo){
    // user obtained item, update the count or add it to the array.
    if (item){
      obtained = [...obtained.filter(i => i.id !== ingredient.id), {id:item.id, count:(item.count + 1)}];
    }
    else{
      obtained.push({id: ingredient.id, count:1});      
    }
  }
  else{
    // user is putting things back.  If the item is undefined, do nothing.
    if (item){
      obtained = [...obtained.filter(i => i.id !== ingredient.id), {id:item.id, count:0}];
    }
  }

  return obtained;
}


