// shows a singleton item that isn't part of a recipe on the receipies list.
import React from 'react';
import { PropTypes } from 'prop-types';

const SimpleListItem = ({ item, ingredients, selected, onClick }) => {
  return (
    <div className={`recipe-item${selected ? '-selected' : ''}`} onClick={onClick}>
      <h3>{item.name} - ${ingredients.map(x=>x.cost).reduce((a,b)=>{return a+b;}).toFixed(2)}</h3>
    </div>
  );
};

SimpleListItem.propTypes ={
  item: PropTypes.object,
  ingredients: PropTypes.array,
  selected: PropTypes.bool,
  onClick: PropTypes.func
};

export default SimpleListItem;


