// Shows a list of all recipies, allows you to select them and push them to a list.

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter, NavLink, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { routes } from '../../constants/routes';
import * as actions from '../../actions/selectedActions';
import RecipeListItem from '../../components/Recipe/RecipeListItem';
import SimpleListItem from '../../components/Recipe/SimpleListItem';

class Recipes extends Component {
  constructor(props, context) {
    super(props, context);

    this.setSelectedState = this.setSelectedState.bind(this);
  }

  setSelectedState(recipe, event){
    event.preventDefault();
    if (this.props.selected.includes(recipe.id)) {
      this.props.actions.deselectRecipe(recipe);
    }else{
      this.props.actions.selectRecipe(recipe);
    }
  }

  render() {
    return (
      <div>
        <h2>Recipe List</h2>
        {this.props.recipes.map(recipe => { 
          return(
            <RecipeListItem
              key={recipe.name}
              recipe={recipe}
              ingredients={recipe.ingredients.map(i => {return this.props.ingredients.find(il => il.id == i);})}
              selected={this.props.selected.indexOf(recipe.id) > -1}
              onClick={(e) => this.setSelectedState(recipe, e)} />
            );
          })
        }
        <h3>Other items</h3>
        {this.props.otherItems.map(recipe => { 
          return(
            <SimpleListItem
              key={recipe.name}
              item={recipe}
              ingredients={recipe.ingredients.map(i => {return this.props.ingredients.find(il => il.id == i);})}
              selected={this.props.selected.indexOf(recipe.id) > -1}
              onClick={(e) => this.setSelectedState(recipe, e)} />
            );
          })
        }
        <Link to={routes.recipe}><button>Add Recipe</button></Link>
        <br/>
        <NavLink to={routes.list}>Go to list</NavLink>
      </div>
    );
  }
}

Recipes.propTypes = {
  actions: PropTypes.object.isRequired,
  recipes: PropTypes.array.isRequired,
  otherItems: PropTypes.array.isRequired,
  ingredients: PropTypes.array.isRequired,
  selected: PropTypes.array.isRequired
};

//boiler plate to connect the store to this component.
function mapStateToProps(state) {
  return {
    recipes: state.recipes.filter(r => r.ingredients.length > 1),
    otherItems: state.recipes.filter(r => r.ingredients.length === 1),
    ingredients: state.ingredients,
    selected: state.selected
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Recipes));


