import * as types from '../actions/actionTypes';
import objectAssign from 'object-assign';
import initialState from './initialState';

export default function obtainedReducer(state = initialState.obtained, action) {

  switch (action.type) {
    case types.OBTAINED_INGREDIENT_SUCCESS:
    case types.OBTAINED_INGREDIENT_UNDO_SUCCESS:
      return [...action.obtained.map(o => objectAssign({}, o))];

    default:
      return state;
  }
}


