import React from 'react';
import { Link } from 'react-router-dom';
import { routes } from '../constants/routes';

const HomePage = () => {
  return (
    <div>
      <h1>Food Errand</h1>
      <ol>
        <li><Link to={routes.recipes}>Recipe list</Link></li>
        <li><Link to={routes.recipe}>Add Recipe</Link></li>
        <li><Link to={routes.list}>See the list!</Link></li>
      </ol>
    </div>
  );
};

export default HomePage;


