// reports to the server that a user has selected a recipe and wants it on their shopping list.
let selected = [];

export function selectRecipe(recipe, undo){
  let index = selected.indexOf(recipe.id);

  if (!undo){
    // Selected item wasn't already in the list, add it
    if (index === -1){
      selected.push(recipe.id);
    }
  }
  else{
    // Deselect the item by removing it from the array, if it is in the array.
    if (index !== -1){
      selected.splice(index, 1);
    }
  }

  return selected;
}


