// Shows a recipe on the recipies list
import React from 'react';
import { PropTypes } from 'prop-types';

const RecipeListItem = ({ recipe, ingredients, selected, onClick }) => {
  return (
    <div className={`recipe-item${selected ? '-selected' : ''}`} onClick={onClick}>
      <h3>{recipe.name} - ${ingredients.map(x=>x.cost).reduce((a,b)=>{return a+b;}).toFixed(2)}</h3>
      {ingredients.sort((a,b) =>{return a.order - b.order;}).map(ingredient => {
        return(<div key={ingredient.name}>{ingredient.name}</div>);
      })}
    </div>
  );
};

RecipeListItem.propTypes ={
  recipe: PropTypes.object,
  ingredients: PropTypes.array,
  selected: PropTypes.bool,
  onClick: PropTypes.func
};

export default RecipeListItem;


