// Shows a recipe on the recipies list
import React from 'react';
import { Glyphicon } from 'react-bootstrap';
import { PropTypes } from 'prop-types';

const ShoppingListQuantity = ({ count, obtainedCount }) => {
  return (
    <span>
      {count > obtainedCount ? [...Array(count)].map((x, i) => <span key={i}> <Glyphicon glyph={i >= obtainedCount ? 'ok-circle' : 'ok-sign'} /></span>) : ''}
    </span>
  );
};

//""

ShoppingListQuantity.propTypes ={
  count: PropTypes.number.isRequired,
  obtainedCount: PropTypes.number.isRequired
};

export default ShoppingListQuantity;


