import * as types from './actionTypes';
import * as api from '../api/selectedApi';

export function selectRecipe(recipe) {
  return function (dispatch) {
    return dispatch({
      type: types.SELECTED_RECIPE_SUCCESS,
      selected: api.selectRecipe(recipe, false)
    });
  };
}

export function deselectRecipe(recipe) {
  return function (dispatch) {
    return dispatch({
      type: types.SELECTED_RECIPE_UNDO_SUCCESS,
      selected: api.selectRecipe(recipe, true)
    });
  };
}


