//displays the items from each recipe in order.  Combines duplicate items into a single item with an indicator of how many.
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import objectAssign from 'object-assign';
import { withRouter, NavLink } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { routes } from '../../constants/routes';
import * as actions from '../../actions/obtainedActions';
import ShoppingListVendor from '../../components/List/ShoppingListVendor';

class ListBuilder extends Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      vendorShowList: props.vendorShowList
    };

    this.setSelectedState = this.setSelectedState.bind(this);
    this.setToggledState = this.setToggledState.bind(this);
  }

  componentWillReceiveProps(nextProps){
    this.setState({vendorShowList: nextProps.vendorShowList});
  }

  setSelectedState(ingredient, event) {
    event.preventDefault();
    if (ingredient.count > (this.props.obtained.find(o => o.id === ingredient.id) || {count:0}).count){
      this.props.actions.obtainIngredient(ingredient);
    }
    else{
      this.props.actions.unobtainIngredient(ingredient);
    }
  }

  setToggledState(vendorName, event) {
    event.preventDefault();
    let vendorShowList = [...this.state.vendorShowList.map(v => objectAssign({}, v))];
    let vendor = vendorShowList.find(v => v.name === vendorName);
    let newList = vendorShowList.filter(v => v.name !== vendor.name).push({name:vendor.name, show:!vendor.show});

    this.setState({vendorShowList: newList});
  }

  render() {
    return (
      <div>
        <h1>Shopping List</h1>
          {this.props.vendors.map(v => {
            return(
              <ShoppingListVendor
                key={v}
                vendorName={v}
                shown={this.state.vendorShowList.find(vendor => v === vendor.name).show}
                onToggleClick={this.setToggledState}
                ingredients={this.props.selectedIngredients.sort((a,b) =>{return a.order - b.order;}).filter(i => i.vendor == v)}
                obtained={this.props.obtained}
                onIngredientClick={this.setSelectedState}/>
              );
            })
          }
        <br/>
        <NavLink to={routes.recipes}>Go to Recipe Selection</NavLink>
      </div>
    );
  }
}

function buildList(recipes, ingredients){
  if (recipes.length > 0){
    let ingredientList = ingredients.map(i => {return {...i, count:0};});

    recipes.forEach(r => {r.ingredients.forEach(i => {ingredientList.find(il => il.id == i).count++;});});

    return ingredientList.filter(i => i.count > 0);
  }else{
    return [];
  }  
}

ListBuilder.propTypes = {
  actions: PropTypes.object.isRequired,
  selectedIngredients: PropTypes.array.isRequired,
  obtained: PropTypes.array.isRequired,
  vendors: PropTypes.array.isRequired,
  vendorShowList: PropTypes.array
};

//boiler plate to connect the store to this component.
function mapStateToProps(state) {
  let vendors = []; 
  let selectedIngredients = buildList(state.recipes.filter(r => state.selected.includes(r.id)), state.ingredients);

  selectedIngredients.forEach( i => {if(!vendors.includes(i.vendor)) {vendors.push(i.vendor);} });
  let vendorShowList = vendors.map(v => {return {name: v, show:true};});

  return {
    selectedIngredients: selectedIngredients,
    obtained: state.obtained,
    vendors: vendors,
    vendorShowList: vendorShowList
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ListBuilder));


