// Allows you to view a recipe and add items to it.
// each item should have a store, an order, and the name of what it is.  Optional: price, 'required'.

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
// import { routes } from '../../constants/routes';
import RecipeForm from '../../components/Recipe/RecipeForm';

class Recipes extends Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      recipe: props.recipe,
      ingredients: props.ingredients
    };

    this.changeHandler = this.changeHandler.bind(this);
  }

  changeHandler(event){
    let recipe = Object.assign(this.state.recipe);
    recipe.name = event.target.value;

    this.setState({recipe: recipe});
  }

  render() {
    return (
      <div>
        <h2>Add Recipe</h2>
        <RecipeForm
          recipe={this.state.recipe}
          ingredients={this.state.ingredients.filter(i => this.state.recipe.ingredients.includes(i.id))}
          onChange={this.changeHandler}
        />
        <button>Add Recipe</button>
      </div>
    );
  }
}

Recipes.propTypes = {
  recipe: PropTypes.object,
  ingredients: PropTypes.array
};

//boiler plate to connect the store to this component.
function mapStateToProps(state) {
  const defaultRecipe = {name:'Test', ingredients:[1,3], selected:false};

  return {
    recipe: defaultRecipe,
    ingredients: state.ingredients
  };
}

export default withRouter(connect(mapStateToProps)(Recipes));


