// Shows a recipe on the recipies list
import React from 'react';
import { PropTypes } from 'prop-types';
import ShoppinglistQuantity from './ShoppingListQuantity';

const shoppingListItem = ({ ingredient, obtainedState, onClick }) => {
  return (
    <div className={`shopping-item${ingredient.count === obtainedState.count ? '-selected' : ''}`} onClick={(e) => onClick(ingredient, e)} >
      <span className="shopping-item-name">{ingredient.name}</span>
      {ingredient.count > 1 ? <ShoppinglistQuantity count={ingredient.count} obtainedCount={obtainedState.count} /> : ''}
    </div>
  );
};

shoppingListItem.propTypes ={
  ingredient: PropTypes.object,
  obtainedState: PropTypes.object,
  onClick: PropTypes.func
};

export default shoppingListItem;


