// Shows a recipe on the recipies list
import React from 'react';
import { PropTypes } from 'prop-types';
import { Glyphicon } from 'react-bootstrap';
import ShoppingListItem from './ShoppingListItem';

const ShoppingListVendor = ({ vendorName, shown, onToggleClick, ingredients, obtained, onIngredientClick }) => {
  return (
    <div>
      <div className="shopping-vendor-title">
        <span className="shopping-vendor">
          Things to get at {vendorName}:
        </span>
        <span className="shopping-vendor-toggle" onClick={(e) => onToggleClick(vendorName, e)}>
          <Glyphicon glyph={shown ? 'menu-up' : 'menu-down'}/>
        </span>
      </div>
      {shown ? ingredients.map(ingredient => {
        const obtainedState = obtained.find(o => o.id === ingredient.id);
        return(
          <ShoppingListItem
            key={ingredient.name}
            ingredient={ingredient}
            obtainedState={obtainedState || {id:ingredient.id, count: 0}}
            onClick={onIngredientClick}/>
          );
        })
      :''}
    </div>
  );
};

ShoppingListVendor.propTypes ={
  vendorName: PropTypes.string.isRequired,
  shown: PropTypes.bool.isRequired,
  onToggleClick: PropTypes.func.isRequired,
  ingredients: PropTypes.array.isRequired,
  obtained: PropTypes.array.isRequired,
  onIngredientClick: PropTypes.func.isRequired
};

export default ShoppingListVendor;


